<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>source</td>
<td>Array&lt;{ text, value }&gt;</td>
<td></td>
<td>原数据列表</td>
</tr>
<tr>
<td>target</td>
<td>Array&lt;{ text, value }&gt;</td>
<td></td>
<td>目标数据列表</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<h3>Events</h3>
<h4>@transfer</h4>
<p>数据转移时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.source</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>原数据列表</td>
</tr>
<tr>
<td>$event.target</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>目标数据列表</td>
</tr>
<tr>
<td>$event.transfer</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>移动的项</td>
</tr>
<tr>
<td>$event.transferValues</td>
<td>Array</td>
<td>移动项的值</td>
</tr>
<tr>
<td>senderVM</td>
<td>Vue</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@change</h4>
<p>数据改变时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.source</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>原数据列表</td>
</tr>
<tr>
<td>$event.target</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>目标数据列表</td>
</tr>
<tr>
<td>$event.transfer</td>
<td>Array&lt;{ text, value }&gt;</td>
<td>移动的项</td>
</tr>
<tr>
<td>$event.transferValues</td>
<td>Array</td>
<td>移动项的值</td>
</tr>
<tr>
<td>senderVM</td>
<td>Vue</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
