<template>
<u-transfer :source="source" :target="target">
</u-transfer>
</template>
<script>
export default {
    data() {
        return {
            source: [
                { text: '水杯', value: 'cup' },
                { text: '坚果', value: 'nut' },
                { text: '毛巾', value: 'towel' },
                { text: '沙发', value: 'sofa', disabled: true },
                { text: '牙刷', value: 'toothbrush', disabled: true },
                { text: '碗', value: 'bowl' },
            ],
            target: [],
        };
    },
};
</script>



